import {
		mapState, 
    } from 'vuex';
import md5 from '../../kayida/common/md5.js'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
var wv;//计划创建的webview

export default {
	components:{uniPopup},
	computed: {
	            ...mapState(['userInfo'])  
	        },
	data() {
		return {
			applyPage:'',
			pitch:1,
			id:'',
			realname:'',
			realnameQ:'',
			card_num:'',
			card_numQ:'',
			mobile:'',
			mobileQ:'',
			apply_tips:``,
			type:'',
			loan:0,
			m:false,
			text:``,
			asseinfo:'',
			query_code:'',
		}
	},
	methods: {
		userAgreement() {
			uni.navigateTo({
				url:"../userareement/userareement"
			})
		},
		changeIpt() {
			this.realname = ''
			this.card_num = ''
			this.mobile = ''
			this.realnameQ = ''
			this.card_numQ = ''
			this.mobileQ = ''
		},
		close() {
			this.$refs.popup.close()
		},
		submitA() {
			this.$refs.popup.open()
		},
		submitApplicationQ() {
			this.$refs.popup.close()
			var p = /^1[3456789]\d{9}$/
			if(this.realnameQ.length < 1) {
				uni.showToast({
					title:"法人姓名不能为空",
					icon:"none"
				})
				return
			}else if(this.card_numQ.length != 18 && this.card_numQ.length != 15) {
				uni.showToast({
					title:"统一社会信用代码格式不正确",
					icon:"none"
				})
				return
			}else if(!(p.test(this.mobileQ))) {
				uni.showToast({
					title:"手机号格式不正确",
					icon:"none"
				})
				return
			}
			if(this.pitch !== 1){
				uni.showToast({
					icon:"none",
					title:"请勾选用户注册协议"
				})
			}else{
				var data = {
					id:this.id,
					realname:this.realnameQ,
					card_num:this.card_numQ,
					mobile:this.mobileQ,
					user_id: this.userInfo.user_id,
					token: this.userInfo.token,
					sign: md5(this.userInfo.user_id+this.userInfo.token)
				}
				this.request.httpRequest('api/loan6/apply',data,'GET').then(res => {
					if(res.data.code===1000){
						uni.showToast({
							title:"提交成功",
							icon:"none"
						})
						uni.navigateTo({
							url:"../applycard/applycard?url="+encodeURIComponent(JSON.stringify(res.data.retval))
						})
					}else{
						uni.showToast({
							icon:"none",
							title:"提交失败，请重试"
						})
					}
				})
				
			}
		},
		submitApplication() {
			this.$refs.popup.close()
			var p = /^1[3456789]\d{9}$/
			if(this.realname.length < 1) {
				uni.showToast({
					title:"姓名不能为空",
					icon:"none"
				})
				return
			}else if(this.card_num.length != 18) {
				uni.showToast({
					title:"身份证格式不正确",
					icon:"none"
				})
				return
			}else if(!(p.test(this.mobile))) {
				uni.showToast({
					title:"手机号格式不正确",
					icon:"none"
				})
				return
			}
			if(this.pitch !== 1){
				uni.showToast({
					icon:"none",
					title:"请勾选用户注册协议"
				})
			}else{
				var data = {
					id:this.id,
					realname:this.realname,
					card_num:this.card_num,
					mobile:this.mobile,
					user_id: this.userInfo.user_id,
					token: this.userInfo.token,
					sign: md5(this.userInfo.user_id+this.userInfo.token)
				}
				if(this.type === 'apply'){
					var data1 = {
						asseinfo:this.asseinfo,
						query_code:this.query_code,
						id:this.id,
						realname:this.realname,
						card_num:this.card_num,
						mobile:this.mobile,
						user_id: this.userInfo.user_id,
						token: this.userInfo.token,
						sign: md5(this.userInfo.user_id+this.userInfo.token)
					}
					this.request.httpRequest('api/card/apply',data1,'GET').then(res => {
						if(res.data.code===1000){
							uni.showToast({
								title:"提交成功",
								icon:"none"
							})
							uni.navigateTo({
								url:"../applycard/applycard?url="+encodeURIComponent(JSON.stringify(res.data.retval))
							})
						}else{
							uni.showToast({
								icon:"none",
								title:"提交失败，请重试"
							})
						}
					})
				}else if(this.type === 'loan'){
					if(this.loan == 0) {
						this.request.httpRequest('api/loan/apply',data,'GET').then(res => {
							if(res.data.code===1000){
								uni.showToast({
									title:"提交成功",
									icon:"none"
								})
								uni.navigateTo({
									url:"../applycard/applycard?url="+encodeURIComponent(JSON.stringify(res.data.retval))
								})
							}else{
								uni.showToast({
									icon:"none",
									title:"提交失败，请重试"
								})
							}
						})
					}else {
						let loan = this.loan
						this.request.httpRequest(`api/loan${loan}/apply`,data,'GET').then(res => {
							if(res.data.code===1000){
								uni.showToast({
									title:"提交成功",
									icon:"none"
								})
								uni.navigateTo({
									url:"../applycard/applycard?url="+encodeURIComponent(JSON.stringify(res.data.retval))
								})
							}else{
								uni.showToast({
									icon:"none",
									title:"提交失败，请重试"
								})
							}
						})
					}
				}else if(this.type === 'credit'){
					this.request.httpRequest('api/credit/apply',data,'GET').then(res => {
						if(res.data.code===1000){
							uni.showToast({
								title:"提交成功",
								icon:"none"
							})
							uni.navigateTo({
								url:"../applycard/applycard?url="+encodeURIComponent(JSON.stringify(res.data.retval))
							})
						}else{
							uni.showToast({
								icon:"none",
								title:"提交失败，请重试"
							})
						}
					})
				}
			}
		}
	},
	onLoad(option) {
		this.id = option.id
		this.type = option.type
		this.loan = option.loan
		let data = {
			id:option.id,
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		if(this.type === 'apply'){
			let data1 = {
				query_code:option.code,
				id:option.id,
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/card/applyPage',data1,'GET').then(res => {
				this.applyPage = res.data.retval
				this.apply_tips = res.data.retval.apply_tips
				this.realname = res.data.retval.realname
				this.card_num = res.data.retval.card_num
				this.mobile = res.data.retval.mobile
				this.asseinfo = res.data.retval.asseinfo
				this.query_code = option.code
				this.text = res.data.retval.prompt_1
			})
		}else if(this.type === 'loan'){
			let loan = option.loan
			if(loan == 0){
				this.request.httpRequest('api/loan/applyPage',data,'GET').then(res => {
					this.applyPage = res.data.retval
					this.apply_tips = res.data.retval.apply_tips
					this.realname = res.data.retval.realname
					this.card_num = res.data.retval.card_num
					this.mobile = res.data.retval.mobile
					this.text = res.data.retval.prompt_1
				})
			}else{
				this.request.httpRequest(`api/loan${loan}/applyPage`,data,'GET').then(res => {
					if(loan == 2){
						this.realnameQ = res.data.retval.realname
						this.card_numQ = res.data.retval.card_num
						this.mobileQ = res.data.retval.mobile
					}else{
						this.realname = res.data.retval.realname
						this.card_num = res.data.retval.card_num
						this.mobile = res.data.retval.mobile
					}
					this.applyPage = res.data.retval
					this.apply_tips = res.data.retval.apply_tips
					this.text = res.data.retval.prompt_1
				})
			}
		}else if(this.type === 'credit'){
			this.request.httpRequest('api/credit/applyPage',data,'GET').then(res => {
				this.applyPage = res.data.retval
				this.apply_tips = res.data.retval.apply_tips
				this.realname = res.data.retval.realname
				this.card_num = res.data.retval.card_num
				this.mobile = res.data.retval.mobile
				this.text = res.data.retval.prompt_1
			})
		}
	}
}
